import React from 'react';
import PropTypes from 'prop-types';
import styles from './style.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { useState } from 'react';
import { BASE_URL } from '../../../../constants/api';
import axios from '../../../../utils/token';

const Index = ({ setAddress, setPopUp, setLocation, setPageNum, setData, setCategory }) => {
  const [searchValue, setSearchVale] = useState('');
  const [searchData, setSearchData] = useState([]);

  const onSearch = async () => {
    try {
      const res = await axios.get(`${BASE_URL}/addresses/search?keyword=${searchValue}`);
      setSearchData(res.data);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className={styles.address_wrap}>
      <form
        className={styles.form}
        onSubmit={e => {
          e.preventDefault();
          if (searchValue.length < 2) {
            alert('2  .');
            return;
          }
          onSearch();
        }}
      >
        <div className={styles.address_search}>
          <input
            type="text"
            placeholder="//, //  ."
            onChange={e => {
              setSearchVale(e.target.value);
            }}
          />
          <button className={styles.btn}>
            <FontAwesomeIcon icon={faSearch} />
          </button>
        </div>
      </form>
      <div className={styles.address}>
        {searchData && (
          <ul>
            {searchData &&
              searchData.map(item => {
                return (
                  <li
                    key={item.rcode}
                    onClick={() => {
                      setData([]);
                      setPageNum(0);
                      setCategory(1);
                      sessionStorage.setItem('pageNum', JSON.stringify(0));
                      setAddress(item);
                      sessionStorage.setItem('addressInfo', JSON.stringify(item));
                      setLocation(true);
                      setPopUp(false);
                    }}
                  >
                    <em>{item.rname}</em>
                  </li>
                );
              })}
          </ul>
        )}
        {searchData && searchData.length == 0 && (
          <div className={styles.non_search}>
            <em>  </em>
          </div>
        )}
      </div>
    </div>
  );
};

Index.propTypes = {
  setAddress: PropTypes.func,
  setPopUp: PropTypes.func,
  setLocation: PropTypes.func,
  setPageNum: PropTypes.func,
  setData: PropTypes.func,
  setCategory: PropTypes.func,
};

export default Index;
