import { useState } from 'react';
import { useEffect } from 'react';

export default function useTime(targetTime) {
  const [time, setTime] = useState(); //   

  const timeForToday = value => {
    const newTime =
      value &&
      value.map(item => {
        const today = new Date();
        const timeValue = new Date(item.createDate);
        const betweenTime = Math.floor((today.getTime() - timeValue.getTime()) / 1000 / 60);
        if (betweenTime < 1) return '';
        if (betweenTime < 60) {
          return `${betweenTime}`;
        }
        const betweenTimeHour = Math.floor(betweenTime / 60);
        if (betweenTimeHour < 24) {
          return `${betweenTimeHour}`;
        }
        const betweenTimeDay = Math.floor(betweenTime / 60 / 24);
        if (betweenTimeDay <= 7) {
          return `${betweenTimeDay}`;
        }
        //7    target 
        const year = new Date(timeValue).getFullYear(); //target  
        const month = new Date(timeValue).getMonth() + 1; //target  
        const day = new Date(timeValue).getDate(); //target  
        const getTime = `${year.toString()} ${('00' + month.toString()).slice(-2)} ${('00' + day.toString()).slice(
          -2,
        )}`;
        return getTime;
        // return `${Math.floor(betweenTimeDay / 365)}`;
      });
    return newTime;
  };

  useEffect(() => {
    const value = timeForToday(targetTime); //   
    setTime(value);
  }, [targetTime]);

  return time;
}
