import Axios from 'axios';
import { getCookie, removeCookie } from './cookie';
import { PATH } from '../constants/path';
const axios = Axios.create({
  baseURL: 'http://13.209.94.72:8080', //API 
});

axios.interceptors.request.use(
  function (config) {
    const accessToken = sessionStorage.getItem('accessToken'); //   refreshToken   
    if (accessToken) {
      config.headers.Authorization = `Bearer ${accessToken}`;
    }
    return config;
  },
  function (error) {
    return Promise.reject(error);
  },
);
// AccessToken  
axios.interceptors.response.use(
  function (response) {
    return response;
  },
  async function (err) {
    const originalConfig = err.config;
    if (err.response.data.code === 6001) {
      try {
        if (err.response.data.message === 'accessToken  ') {
          throw Error(' ');
        }
        let refreshToken = getCookie('refreshToken'); //   refreshToken   
        let accessToken = sessionStorage.getItem('accessToken'); //   accessToken   
        const res = await Axios({
          url: `http://13.209.94.72:8080/auth/reissue`, //refreshToken   API
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          data: {
            accessToken,
            refreshToken,
          },
          withCredentials: true,
        });
        if (res.status === 200) {
          sessionStorage.setItem('accessToken', res.data.accessToken);
          return await axios.request(originalConfig);
        }
      } catch (err) {
        if (err.response.data.code === 6003 || err.response.data.code === 6005) {
          sessionStorage.removeItem('accessToken');
          removeCookie('refreshToken');
          history.pushState(null, null, location.origin + PATH.LOGIN);
          window.location.reload();
        }
      }
      return Promise.reject(err);
    }
    return Promise.reject(err);
  },
);

export default axios;
