import React, { useEffect } from 'react';
import Axios from 'axios';
import styles from './style.module.css';
import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'; //
import { faExclamationCircle } from '@fortawesome/free-solid-svg-icons'; //
import { useNavigate } from 'react-router-dom';
import TermsService from './TermsService';
import { PATH } from '../../../constants/path';
import { USER_URL } from '../../../constants/api';

const Index = () => {
  const navigate = useNavigate();
  const [serviceCheck, setServiceCheck] = useState(false);
  const [infoCheck, setInfoCheck] = useState(false);
  const [inputValue, setInputValue] = useState({
    userPassword: '',
    userPasswordCheck: '',
    userEmail: '',
    userName: '',
  });
  const [userEmailCheck, setUserEmailCheck] = useState(false); //  
  const [EmailLengthCheck, setEmailLengthCheck] = useState(false); //  
  const [userPasswordCheck, setUserPasswordCheck] = useState(false); // 
  const [PasswordCheck, setPasswordCheck] = useState(false); //  
  const [userNameCheck, setUserNameCheck] = useState(false); // 
  const [allCheck, setAllCheck] = useState(false); //   

  // API 
  const joinData = {
    email: inputValue.userEmail,
    password: inputValue.userPassword,
    passwordConfirm: inputValue.userPasswordCheck,
    nickname: inputValue.userName,
  };

  //   
  const sendJoinForm = async () => {
    try {
      const res = await Axios.post(`http://13.209.94.72:8080${USER_URL.SIGNUP} `, joinData);
      console.log(res);
      navigate(`${PATH.LOGIN}`);
    } catch (error) {
      console.log(error);
    }
  };

  //   
  const specialLetter = inputValue.userPassword.search(/[`~!@#$%^&*|'";:/?]/gi);

  // 1   8  true 
  const validPassword = specialLetter >= 0 && inputValue.userPassword.length >= 8;

  // @ .  
  const validEmail = inputValue.userEmail.includes('@') && inputValue.userEmail.includes('.');

  //input   inputValue 
  const handleInput = event => {
    const { name, value } = event.target;
    setInputValue({
      ...inputValue,
      [name]: value,
    });
  };

  //input  
  const inputCheck = () => {
    // 
    if (validEmail == true) {
      setUserEmailCheck(true);
    } else {
      setUserEmailCheck(false);
    }

    //  
    if (inputValue.userEmail.length >= 5) {
      setEmailLengthCheck(true);
    } else {
      setEmailLengthCheck(false);
    }

    //   
    if (validPassword == true) {
      setUserPasswordCheck(true);
    } else {
      setUserPasswordCheck(false);
    }

    //  
    if (inputValue.userPassword == inputValue.userPasswordCheck) {
      setPasswordCheck(true);
    } else {
      setPasswordCheck(false);
    }

    // 2  
    if (inputValue.userName.length >= 2) {
      setUserNameCheck(true);
    } else {
      setUserNameCheck(false);
    }
  };

  //input true,false   
  useEffect(() => {
    inputCheck();
  }, [inputValue]);

  //    
  const handleAllCheck = () => {
    if (
      PasswordCheck == true &&
      userEmailCheck == true &&
      userNameCheck == true &&
      serviceCheck == true &&
      infoCheck == true
    ) {
      setAllCheck(true);
    } else {
      setAllCheck(false);
    }
  };

  //      
  useEffect(() => {
    handleAllCheck();
  }, [PasswordCheck, userEmailCheck, userNameCheck, serviceCheck, infoCheck]);

  return (
    <form className={styles.join_form}>
      <div className={`${styles.email} ${styles.join_input_box}`}>
        <input
          type="text"
          placeholder=""
          name="userEmail"
          onChange={e => {
            handleInput(e);
          }}
        />
        {userEmailCheck == false ? (
          <div className={styles.warning}>
            <FontAwesomeIcon icon={faExclamationCircle} />
            <p> </p>
          </div>
        ) : null}
        {EmailLengthCheck == false ? (
          <div className={styles.warning}>
            <FontAwesomeIcon icon={faExclamationCircle} />
            <p>5  </p>
          </div>
        ) : null}
      </div>
      <div className={`${styles.password} ${styles.join_input_box}`}>
        <input
          type="password"
          placeholder=""
          name="userPassword"
          onChange={e => {
            handleInput(e);
          }}
        />
        <input
          type="password"
          placeholder=" "
          name="userPasswordCheck"
          onChange={e => {
            handleInput(e);
          }}
        />
        {userPasswordCheck == false ? (
          <div className={styles.warning}>
            <FontAwesomeIcon icon={faExclamationCircle} />
            <p>8 , + , </p>
          </div>
        ) : null}
        {PasswordCheck == false ? (
          <div className={styles.warning}>
            <FontAwesomeIcon icon={faExclamationCircle} />
            <p> </p>
          </div>
        ) : null}
      </div>
      <div className={`${styles.name} ${styles.join_input_box}`}>
        <input
          type="text"
          placeholder=""
          name="userName"
          onChange={e => {
            handleInput(e);
          }}
        />
        {userNameCheck == false ? (
          <div className={styles.warning}>
            <FontAwesomeIcon icon={faExclamationCircle} />
            <p> </p>
          </div>
        ) : null}
      </div>
      <TermsService
        serviceCheck={serviceCheck}
        setServiceCheck={setServiceCheck}
        infoCheck={infoCheck}
        setInfoCheck={setInfoCheck}
      ></TermsService>
      {allCheck == true ? (
        <button
          className={styles.join_btt1}
          onClick={e => {
            e.preventDefault();
            sendJoinForm();
          }}
        >
            
        </button>
      ) : (
        <button
          className={styles.join_btt2}
          onClick={e => {
            e.preventDefault();
          }}
        >
            
        </button>
      )}
    </form>
  );
};

export default Index;
