import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import styles from './style.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'; //
import { faAngleRight } from '@fortawesome/free-solid-svg-icons'; //

const Index = ({ serviceCheck, setServiceCheck, infoCheck, setInfoCheck }) => {
  const [allCheck, setAllCheck] = useState(false); //  
  const [eventCheck, setEventCheck] = useState(false); //  

  //  
  const allBtnEvent = () => {
    if (allCheck === false) {
      setAllCheck(true);
      setServiceCheck(true);
      setEventCheck(true);
      setInfoCheck(true);
    } else {
      setAllCheck(false);
      setServiceCheck(false);
      setEventCheck(false);
      setInfoCheck(false);
    }
  };

  //  
  const serviceBtnEvent = () => {
    if (serviceCheck === false) {
      setServiceCheck(true);
    } else {
      setServiceCheck(false);
    }
  };

  //  
  const eventBtnEvent = () => {
    if (eventCheck === false) {
      setEventCheck(true);
    } else {
      setEventCheck(false);
    }
  };

  //    
  const infoBtnEvent = () => {
    if (infoCheck === false) {
      setInfoCheck(true);
    } else {
      setInfoCheck(false);
    }
  };
  //input true,false  all input  
  useEffect(() => {
    if (serviceCheck === true && eventCheck === true && infoCheck === true) {
      setAllCheck(true);
    } else {
      setAllCheck(false);
    }
  }, [serviceCheck, eventCheck, infoCheck]);

  return (
    <div className={styles.terms_service}>
      <div className={styles.all_check_box}>
        <input
          type="checkbox"
          id="all_check"
          checked={allCheck}
          onChange={() => {
            allBtnEvent();
          }}
        />
        <label htmlFor="all_check">    </label>
      </div>
      <span className={styles.line}></span>
      <div className={styles.check_box}>
        <input
          type="checkbox"
          id="check1"
          checked={serviceCheck}
          onChange={() => {
            serviceBtnEvent();
          }}
        />
        <label htmlFor="check1"> ()</label>
        <p>
            <FontAwesomeIcon icon={faAngleRight} />
        </p>
      </div>
      <div className={styles.check_box}>
        <input
          type="checkbox"
          id="check2"
          checked={eventCheck}
          onChange={() => {
            eventBtnEvent();
          }}
        />
        <label htmlFor="check2">,    ()</label>
        <p>
            <FontAwesomeIcon icon={faAngleRight} />
        </p>
      </div>
      <div className={styles.check_box}>
        <input
          type="checkbox"
          id="check3"
          checked={infoCheck}
          onChange={() => {
            infoBtnEvent();
          }}
        />
        <label htmlFor="check3">     ()</label>
        <p>
            <FontAwesomeIcon icon={faAngleRight} />
        </p>
      </div>
    </div>
  );
};

Index.propTypes = {
  serviceCheck: PropTypes.bool,
  setServiceCheck: PropTypes.func,
  infoCheck: PropTypes.bool,
  setInfoCheck: PropTypes.func,
};

export default Index;
