import React from 'react';
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import { useEffect } from 'react';
import styles from './style.module.css';
import { setCookie } from '../../../utils/cookie';
import { useNavigate } from 'react-router-dom';

const Form = () => {
  const { register, handleSubmit, getValues, formState } = useForm();
  const nav = useNavigate();
  // input   email value

  //   
  const sendLoginData = async () => {
    //login api data
    const email = getValues('email');
    const password = getValues('password');

    const loginRequest = {
      email,
      password,
    };
    try {
      const response = await axios.post(`http://13.209.94.72:8080/auth/login`, loginRequest);
      if (response) {
        sessionStorage.setItem('accessToken', response.data.accessToken);
        setCookie('refreshToken', response.data.refreshToken);
        nav('/');
      }
    } catch (err) {
      console.log(err);
    }
  };
  // form  input, error   useEffect .
  useEffect(() => {}, [formState]);
  return (
    <>
      <form className={styles.form} onSubmit={handleSubmit(sendLoginData)}>
        <span className={styles.span}>
          <span className={styles.spanICon}>*</span>
          <span className={styles.error}>{formState.errors.email?.message}</span>
        </span>
        <input
          className={styles.input}
          {...register('email', {
            required: '  .',
            // email 
          })}
          type="text"
          placeholder=" "
        ></input>
        <span className={styles.span2}>
          <span className={styles.spanICon}>*</span>
          <span className={styles.error}>{formState.errors.password?.message}</span>
        </span>
        <input
          className={styles.input}
          {...register('password', {
            required: ' .',
          })}
          type="password"
          placeholder=" "
        ></input>
        <div className={styles.passwordBox}>
          <Link to="#">
            <span> </span>
          </Link>
        </div>
        <button className={styles.btn}></button>
        <button className={styles.btn}></button>
      </form>
    </>
  );
};

export default Form;
