import React from 'react';
import styles from './style.module.css';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart } from '@fortawesome/free-solid-svg-icons';
import { useState } from 'react';
import { BASE_URL } from '../../../constants/api';
import axios from '../../../utils/token';
import TwoComment from './TwoComment';
import { useEffect } from 'react';

const Index = ({ newComment, id, commentHandleData, commentPage }) => {
  const [tabTwoComment, setTabTwoComment] = useState(); //      
  const [commentValue, setCommentValue] = useState(); //input  
  const [commentPut, setCommentPut] = useState(0); // ,  
  const [commentPutTarget, setCommentPutTarget] = useState(); //  ID 
  const [commentValue2, setCommentValue2] = useState(); // input  
  const [commentPut2, setCommentPut2] = useState(0); // ,  

  const [currentPageGroup, setCurrentPageGroup] = useState(1); //  
  const [page, setPage] = useState();

  //    
  useEffect(() => {
    let commentPageCopy = [...commentPage];
    commentPageCopy = commentPageCopy.slice(0, 5);
    setPage(commentPageCopy);
  }, [commentPage]);

  function pagePlus() {
    setCurrentPageGroup(prev => prev + 1);
    let commentPageCopy = [...commentPage];
    commentPageCopy = commentPageCopy.slice(currentPageGroup * 5, (currentPageGroup + 1) * 5);
    setPage(commentPageCopy);
  }

  function pageMinus() {
    setCurrentPageGroup(currentPageGroup - 1);
    let commentPageCopy = [...commentPage];
    commentPageCopy = commentPageCopy.slice(currentPageGroup * 5, (currentPageGroup + 1) * 5);
    setPage(commentPageCopy);
  }

  console.log(page);

  // 
  const handleComment = async () => {
    try {
      if (commentPut == 0) {
        await axios.post(`${BASE_URL}/posts/${id}/comments`, { content: commentValue });
      } else {
        await axios.patch(`${BASE_URL}/posts/${id}/comments/${commentPutTarget}`, { content: commentValue });
        setCommentPut(0);
      }

      setCommentValue('');
      commentHandleData();
    } catch (error) {
      console.log(error);
    }
  };

  // 
  const handleCommentRemove = async commentId => {
    try {
      await axios.delete(`${BASE_URL}/posts/${id}/comments/${commentId}`);
      commentHandleData();
    } catch (error) {
      console.log(error);
    }
  };

  // 
  const handleCommentLike = async commentId => {
    try {
      await axios.put(`${BASE_URL}/comments/${commentId}/addLike`);
      commentHandleData();
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className={styles.comment_wrap}>
      <form
        className={styles.my_form}
        onSubmit={e => {
          e.preventDefault();
          handleComment();
        }}
      >
        <div className={styles.comment_input}>
          <div className={styles.input_box}>
            <div className={styles.user_img}></div>
            <input
              type="text"
              placeholder="       :)"
              value={commentValue || ''}
              onChange={e => {
                setCommentValue(e.target.value);
              }}
            />
            <button className={styles.btn}></button>
          </div>
          {commentPut == 1 && (
            <div className={styles.cancel}>
              <em
                onClick={() => {
                  setCommentPut(0);
                  setCommentValue('');
                }}
              >
                 
              </em>
            </div>
          )}
        </div>
      </form>
      <div className={styles.comment_list}>
        {newComment && (
          <ul>
            {newComment.map(item => {
              return (
                <li key={item.id}>
                  <div className={styles.one_depth_comment}>
                    <div className={styles.user_img}></div>
                    <div className={styles.comment}>
                      <h4>{item.member.nickname}</h4>
                      <p>{item.content} </p>
                      <div className={styles.comment_info}>
                        <ul>
                          <li>
                            <em>{item.newTime}</em>
                          </li>
                          <li
                            onClick={() => {
                              setTabTwoComment(item.id);
                              setCommentValue('');
                              setCommentValue2('');
                              setCommentPut2(0);
                            }}
                          >
                            <em> </em>
                          </li>
                          {item.owner == false && (
                            <li
                              onClick={() => {
                                handleCommentLike(item.id);
                              }}
                              className={`${item.liked == true && styles.active}`}
                            >
                              <FontAwesomeIcon icon={faHeart} className={styles.icon} />
                              <em></em>
                            </li>
                          )}
                          {item.owner == false && (
                            <li>
                              <em></em>
                            </li>
                          )}
                          {item.owner == true && (
                            <li
                              onClick={() => {
                                setCommentPut(1);
                                setCommentValue(item.content);
                                setCommentPutTarget(item.id);
                                setTabTwoComment(-1);
                              }}
                            >
                              <em></em>
                            </li>
                          )}
                          {item.owner == true && (
                            <li
                              onClick={() => {
                                handleCommentRemove(item.id);
                              }}
                            >
                              <em></em>
                            </li>
                          )}
                        </ul>
                      </div>
                    </div>
                  </div>
                  <TwoComment
                    item={item}
                    commentHandleData={commentHandleData}
                    id={id}
                    tabTwoComment={tabTwoComment}
                    setTabTwoComment={setTabTwoComment}
                    handleCommentRemove={handleCommentRemove}
                    handleCommentLike={handleCommentLike}
                    commentValue2={commentValue2}
                    setCommentValue2={setCommentValue2}
                    commentPut2={commentPut2}
                    setCommentPut2={setCommentPut2}
                  ></TwoComment>
                </li>
              );
            })}
          </ul>
        )}
        <div className={styles.page_number}>
          <ul>
            <li
              onClick={() => {
                pageMinus();
              }}
            >
              <em></em>
            </li>
            <li>
              <em>1</em>
            </li>
            <li>
              <em>2</em>
            </li>
            <li>
              <em>3</em>
            </li>
            <li>
              <em>4</em>
            </li>
            <li>
              <em>5</em>
            </li>
            <li
              onClick={() => {
                pagePlus();
              }}
            >
              <em></em>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

Index.propTypes = {
  newComment: PropTypes.array,
  id: PropTypes.string,
  commentHandleData: PropTypes.func,
  commentPage: PropTypes.array,
};

export default Index;
