import React from 'react';
import { useState } from 'react';
import styles from './style.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart, faBell, faBars, faTimes } from '@fortawesome/free-solid-svg-icons';
import usePopupClose from '../../../hooks/usePopupClose';
import { useRef } from 'react';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { PATH } from '../../../constants/path';

const Index = () => {
  const navigate = useNavigate();
  const [login] = useState(false); //  
  const [userActive, setUserActive] = useState(false); //  1024        active
  const target = useRef(null); // 
  const close = usePopupClose(target); //     

  useEffect(() => {
    setUserActive(close);
  }, [close]);
  return (
    <div ref={target}>
      {login === true && (
        <div
          className={styles.mobile}
          onClick={() => {
            setUserActive(!userActive);
          }}
        >
          {userActive === false && <FontAwesomeIcon icon={faBars} />}
          {userActive === true && <FontAwesomeIcon icon={faTimes} />}
        </div>
      )}
      <div
        className={`${styles.user_info} ${login === true && styles.visible} ${userActive === true && styles.active}`}
      >
        {login === false ? (
          <div className={styles.non_login}>
            <ul>
              <li>
                <em
                  onClick={() => {
                    navigate(PATH.LOGIN);
                  }}
                >
                  
                </em>
              </li>
              <li>
                <em
                  onClick={() => {
                    navigate(PATH.SIGNUP);
                  }}
                >
                  
                </em>
              </li>
            </ul>
          </div>
        ) : (
          <div className={styles.on_login}>
            <ul>
              <li>
                <div className={styles.icon_box}>
                  <ul>
                    <li>
                      <FontAwesomeIcon icon={faHeart} className={styles.icon} />
                    </li>
                    <li>
                      <FontAwesomeIcon icon={faBell} className={styles.icon} />
                    </li>
                  </ul>
                </div>
              </li>
              <li>
                <em></em>
              </li>
              <li>
                <div className={styles.user}>
                  <div className={styles.userImg}></div>
                  <em> </em>
                </div>
              </li>
              <li>
                <em></em>
              </li>
            </ul>
          </div>
        )}
      </div>
    </div>
  );
};

export default Index;
